.main {
    margin-top: var(--header-h);
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.main__md_container {
    padding-right: calc(var(--container-padding) / 2);
}
.main__preview_container{
    padding-left: calc(var(--container-padding) / 2);
}
.main__md_container, .main__preview_container {
    width: 50%;
    height: 100%;
    padding: var(--container-padding);
}

.main__btn_scroll {
    display: none;
    height: 12%;
}

