        /*justify-content: space-around;*/
    /*}*/

/*}*/
@media screen and (max-width: 768px){
    .main__md_container, .main__preview_container {
        padding: 15px;
    }
}
@media screen and (max-width: 498px) and (orientation: portrait) {
    .main {
        -ms-flex-pack: center;
            justify-content: center;
    }
    .main__md_container, .main__preview_container {
        padding: 10px 5px 0;
        width: 90%;
        height: 88%;
    }
    .main__btn_scroll {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }

}
@media screen and (max-width: 498px) and (orientation: landscape) {
    .main__md_container, .main__preview_container {
        padding: 5px;
        width: 50%;
    }
}



