header {
    width: 100%;
    background-color: var(--dark-blue);
    color: #EFF1F3;
    padding: 8px var(--container-padding);
    position: fixed;
    height: var(--header-h);
}

.header__logo {
    width: 45%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.header__logo_text {
    margin-left: 10px;
    font-size: 1.35rem;
}

@media screen and (max-width: 768px) {
    .header__logo {
        width: 80%;
    }
    .header__logo_text {
        font-size: 1.1rem;
    }
}


